import React from 'react';
import './Notes.css';
import { BrowserRouter as Router, Route, Link} from "react-router-dom";


var Prism = require('prismjs');
var loadLanguages = require('prismjs/components/index.js');

class App extends React.Component {
    render() {
        return (
            <Router>
                <Route path="/notes/*" component={
                    ({match}) => {
                        if (match.url === '/notes/') {
                            return <Menu/>;
                        } else {
                            return <Editor file={match.url.replace('/notes/', '')}/>;
                        }
                    }
                }/>
          </Router>
        );
    }
}

class Menu extends React.Component {
    constructor(props) {
        super(props);
        this.state = {keys: []};
      }

    componentDidMount() {
        window.addEventListener('pageshow', (event) => {
            caches.open('files').then((cache) => {
                cache.keys().then(keys => {
                    keys.forEach((key) => {
                        console.log(key.url);
                        let matches = key.url.replace('https://18nshinagawa.github.io/notes/', '').split('/');
                        console.log(matches);
                        for (let match in matches) {
                            let div = document.createElement('div');
                            div.id = matches[match];
                            div.innerText = matches[match];
                            if (match === 0) {
                                document.getElementById('files').append(div);
                            } else if (match === matches.length - 1) {
                                div.innerHTML = '<a href="' + key.url.replace('https://18nshinagawa.github.io/notes/', '') + '"</a>';
                                document.getElementById(matches[match - 1]).append(div);
                            } else {
                                document.getElementById(matches[match - 1]).append(div);
                            }
                        }
                    });
                });
            });
        });
    }

    render () {
        return (
            <div id="container">
                <div id="files"></div>
            </div>
        );
    }
}

class Editor extends React.Component {
    heading = 0;
    math = 0;
    objects = {math: ['math'], code: ['code', 'language'], image: ['url', 'text'], link: ['url', 'text']};
    object = '';
    display = false;
    style = `
    <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/katex@0.10.1/dist/katex.min.css" integrity="sha384-dbVIfZGuN1Yq7/1Ocstc1lUEm+AT+/rCkibIcC/OmWo5f0EA48Vf8CytHzGrSwbQ" crossorigin="anonymous">
    <style>

    body {
        margin: 0;
        background-color: #333333;
        overflow: hidden;
    }

    #editor {
        width: 16cm;
        padding: 2.5cm;
        background-color: white;
        color: #333333;
        outline: none;
        font-family: 'georgia', serif;
        line-height: 1.5;
        word-break: break-word;
        caret-color: #4488FF;
    }
    
    ::-moz-selection {
        background-color: rgba(68, 136, 255, 0.25);
    }
    
    #editor > :first-child {
        margin-top: 0;
    }
    
    #editor > :last-child {
        margin-bottom: 0;
    }

    h1, h2, h3 {
        text-align: center;
        font-weight: normal;
    }
    
    img {
        width: 10.5cm;
        display: block;
        margin: auto;
    }

    .caption {
        text-align: center;
        margin-top: 0.5em;
    }

    .object {
        -moz-user-select: all;
    }

    .object[data-object="math"] {
        max-width: 16cm;
        overflow: hidden;
        display: inline-flex;
    }
    
    .object[data-object="math"].display {
        display: block;
    }

    a {
        color: #4488FF;
    }
    
    blockquote {
        margin: 1em 0;
        padding: 1em;
        border-left: 2px solid #333333;
    }

    blockquote > :first-child {
        margin-top: 0;
    }
    
    blockquote > :last-child {
        margin-bottom: 0;
    }
    
    ul, ol {
        color: inherit;
    }

    [data-object="code"] {
        background-color: #EEEEEE;
        font-family: 'monaco';
        max-width: 16cm;
        overflow: hidden;
        display: inline;
        color: #0088FF;
    }

    [data-object="code"].display {
        white-space: pre-wrap;
        display: block;
        padding: 8px;
        margin: 1em 0;
    }

    .token.string {
        color: #DD4433;
    }

    .token.keyword {
        color: #4488FF;
    }

    .token.function {
        color: #FF8800;
    }

    .token.class-name {
        color: #119955;
    }

    .token.punctuation {
        color: #999999;
    }

    .token.comment {
        color: #9977BB;
    }

    .token.operator {
        color: #888888;
    }

    table {
        border-collapse: collapse;
        margin: auto;
    }

    td, th {
        border: 2px solid black;
        padding: 8px;
    }
    
    .page-break {
        display: block;
        height: 0.3125cm;
        width: calc(100% + 5cm);
        background-color: #333333;
        left: -2.5cm;
        position: relative;
        margin: 2.5cm 0;
        -moz-user-select: none;
    }
    
    @media print {
        #editor {
            margin: 0;
            padding: 0;
            color-adjust: exact;
        }

        .page-break {
            page-break-after: always;
            visibility: hidden;
        }
    }

    @page {
        margin: 2.5cm;
    }

    </style>
    `

    componentDidMount() {
        let iframe = document.getElementById('iframe').contentWindow.document;

        caches.open('files').then((cache) => {
            cache.match('/notes/' + this.props.file).then((response) => 
                response.text()
            ).then((text) => {
                iframe.getElementById('editor').innerHTML = text;
                document.getElementById('iframe').style.height = iframe.getElementById('editor').clientHeight + 'px';
            }).catch((error) => {
                console.warn(error);
                document.getElementById('iframe').style.height = iframe.getElementById('editor').clientHeight + 'px';
            })
        });

        iframe.open();
        iframe.write(this.style + '<div id="editor" spellcheck="false" contentEditable="true"></div>');
        iframe.close();

        document.querySelector('title').innerText = this.props.file.replace(/.*\//, '');

        Array.from(iframe.getElementsByTagName('h1')).forEach((element, index) => {
            element.id = element.innerText.replace(/\s/g, '');
            if (index === 0) {
                document.querySelector('title').innerText = element.innerText;
            }
        });
        
        Array.from(iframe.querySelectorAll('h2, h3')).forEach((element) => {
            element.id = element.innerText.replace(/\s/g, '');
        });

        document.getElementById('iframe').contentWindow.addEventListener('scroll', (event) => {
            document.getElementById('options').style.transform = 'translateY(-' + iframe.body.scrollTop + 'px)';
        });

        iframe.getElementById('editor').addEventListener('click', (event) => {
            this.handleClickIframe(event);
        });

        iframe.execCommand("defaultParagraphSeparator", false, 'p');
        
        document.addEventListener('keydown', (event) => {
            if ('sop'.indexOf(event.key.toLowerCase()) !== -1) {
                event.preventDefault();
                this.handleKeyDownIframe(event);
            }
        });
        
        iframe.getElementById('editor').addEventListener('keydown', (event) => {
            this.handleKeyDownIframe(event);
        });

        iframe.getElementById('editor').addEventListener('keyup', (event) => {
            this.handleKeyUpIframe(event);
        });

        iframe.getElementById('editor').addEventListener('input', (event) => {
            this.handleInputIframe(event);
        });
    }

    handleKeyDownIframe(event) {
        let iframe = document.getElementById('iframe').contentWindow.document;
        let selection = document.getElementById('iframe').contentWindow.getSelection();
        let range = selection.getRangeAt(0);

        if (range.commonAncestorContainer.nodeType === 3 && range.commonAncestorContainer.parentElement.id === 'editor') {
            iframe.execCommand('formatBlock', false, 'p');
        }

        if ((navigator.platform === 'MacIntel' && event.metaKey) || (navigator.platform !== 'MacIntel' && event.ctrlKey)) {
            if (event.key.toLowerCase() === 'x' && event.shiftKey) {
                event.preventDefault();
            } else if ('123'.indexOf(event.key) !== -1) {
                event.preventDefault();
                iframe.execCommand('heading', false, 'h' + event.key);
            } else if (event.key === '0') {
                event.preventDefault();
                iframe.execCommand('formatBlock', false, 'p');
            } else if (event.key.toLowerCase() === 'l') {
                event.preventDefault();
                iframe.execCommand(event.shiftKey ? 'insertOrderedList' : 'insertUnorderedList');
            } else if (event.key === 'b') {
                event.preventDefault();
                iframe.execCommand('bold');
            } else if (event.key === 'i') {
                event.preventDefault();
                iframe.execCommand('italic');
            } else if ('jghm'.indexOf(event.key.toLowerCase()) !== -1) {
                event.preventDefault();
                if (event.shiftKey) {
                    this.display = true;
                    document.getElementById('display').classList.add('display');
                } else {
                    this.display = false;
                    document.getElementById('display').classList.remove('display');
                }

                this.object = {j: 'code', m: 'math', h: 'link', g: 'image'}[event.key.toLowerCase()];
                iframe.execCommand('insertHTML', false, '<span class="selection ' + (this.display ? 'display': '') + '"></span>');
                document.getElementById('first').dataset.placeholder = this.objects[this.object][0];
                document.getElementById('second').dataset.placeholder = this.objects[this.object][1];
                document.getElementById('options').style.top = iframe.querySelector('.selection').getBoundingClientRect().top + iframe.body.scrollTop + 'px';
                document.getElementById('options').style.transform = 'translateY(-' + iframe.body.scrollTop + 'px)';
                event.preventDefault();
                document.getElementById('options').classList.add('focus');
                document.getElementById('first').innerHTML = '<br>';
                document.getElementById('second').innerHTML = '<br>';
                document.getElementById('first').focus();
            } else if (event.key === 'p') {
                event.preventDefault();
                document.getElementById('iframe').contentWindow.print();
            } else if (event.key.toLowerCase() === 's') {
                event.preventDefault();

                if (event.shiftKey) {
                    let blob = new Blob([this.style + '<div id="editor">' + iframe.getElementById('editor').innerHTML + '</div>'], {type: 'text/html'});
                    let a = window.document.createElement('a');
                    a.href = window.URL.createObjectURL(blob);
                    a.download = (document.querySelector('title').innerText || 'untitled') + '.html';
                    document.body.appendChild(a);
                    a.click();        
                    document.body.removeChild(a);
                } else {
                    caches.open('files').then((cache) => {
                        cache.put('/notes/' + this.props.file, new Response(iframe.getElementById('editor').innerHTML));
                    });
                }
            } else if (event.key === 'o') {
                event.preventDefault();
                document.getElementById('open').click();
            } else if (event.key === 'Enter') {
                event.preventDefault();
                let selection = document.getElementById('iframe').contentWindow.getSelection();
                let range = selection.getRangeAt(0);
                let p = false;
                
                let end = false;

                if (range.commonAncestorContainer.nodeType === 3 && range.commonAncestorContainer.parentElement.id === 'editor') {
                    iframe.execCommand('formatBlock', false, 'p');
                }

                if (range.commonAncestorContainer.nodeType !== 3 && (!range.nextSibling || (range.nextSibling.classList && range.nextSibling.classList.contains('page-break')))) {
                    p = true;
                }

                if (range.commonAncestorContainer.nodeType !== 3 && range.Offset === range.commonAncestorContainer.childNodes.length) {
                    p = true;
                }

                if ((!range.commonAncestorContainer.nextSibling || range.commonAncestorContainer.nextSibling.nodeName === 'BR') && range.commonAncestorContainer.nodeValue && range.commonAncestorContainer.nodeValue.length === range.endOffset) {
                    end = true;
                    if (!range.commonAncestorContainer.parentElement.nextSibling || (range.commonAncestorContainer.parentElement.nextSibling.classList && range.commonAncestorContainer.parentElement.nextSibling.classList.contains('page-break'))) {
                        p = true;
                    }
                }

                if (range.startOffset === 0 && range.startContainer.parentElement.firstChild === range.startContainer) {
                    if (!range.startContainer.parentElement.previousSibling || (range.startContainer.parentElement.previousSibling.classList && range.startContainer.parentElement.previousSibling.classList.contains('page-break'))) {
                        return;
                    }
                }

                if (range.startContainer.nodeType !== 3 && range.startOffset === 0) {
                    if (!range.startContainer.previousSibling) {
                        return;
                    }
                }

                if (range.startOffset === 0 && range.commonAncestorContainer.parentElement === iframe.getElementById('editor').firstChild) {
                    return;
                }

                if (range.commonAncestorContainer.parentElement.id === 'editor') {
                    if (range.commonAncestorContainer.previousSibling && range.commonAncestorContainer.previousSibling.classList.contains('page-break')) {
                        if (!range.commonAncestorContainer.previousSibling || range.commonAncestorContainer.previousSibling.classList.contains('page-break')) {
                            return;
                        }
                    }
                }
                
                if (p) {
                    iframe.execCommand('insertHTML', false, '<div class="page-break"><br></div><p><br></p>');
                } else {
                    iframe.execCommand('insertHTML', false, '<div class="page-break"><br></div>');
                    if (!end) {
                        let newRange = iframe.createRange();
                        if (range.commonAncestorContainer.nodeType === 3) {
                            newRange.setStart(range.commonAncestorContainer.parentElement, 0);
                            newRange.setEnd(range.commonAncestorContainer.parentElement, 0);
                        } else {
                            newRange.setStart(range.commonAncestorContainer, 0);
                            newRange.setEnd(range.commonAncestorContainer, 0);
                        }
                        selection.removeAllRanges();
                        selection.addRange(newRange);
                    } else {
                        let newRange = iframe.createRange();
                        newRange.setStart(range.commonAncestorContainer.parentElement.nextSibling.nextSibling, 0);
                        newRange.setEnd(range.commonAncestorContainer.parentElement.nextSibling.nextSibling, 0);
                        selection.removeAllRanges();
                        selection.addRange(newRange);
                    }
                }
            } else if (event.key === 'Backspace') {
                let selection = document.getElementById('iframe').contentWindow.getSelection();
                let range = selection.getRangeAt(0);
                if (range.startOffset === 0 && range.startContainer.parentElement.previousSibling && range.startContainer.parentElement.previousSibling.classList.contains('page-break')) {
                    event.preventDefault();
                } else if (range.commonAncestorContainer.previousSibling && range.commonAncestorContainer.previousSibling.classList.contains('page-break')) {
                    event.preventDefault();
                }
            }
        } else if (event.key === 'Tab') {
            event.preventDefault(); 
            if (event.shiftKey) {
                iframe.execCommand('outdent');
            } else {
                iframe.execCommand('indent');
            }
        } else if (event.key === 'Backspace') {
            let selection = document.getElementById('iframe').contentWindow.getSelection();
            let range = selection.getRangeAt(0);
            
            if (range.startContainer === range.endContainer && range.startOffset === range.endOffset) {
                if (range.startContainer.previousSibling && range.startOffset === 0 && (range.startContainer.previousSibling.classList.contains('object'))) {
                    event.preventDefault();
                    selection.removeAllRanges();
                    range.selectNode(range.startContainer.previousSibling);
                    selection.addRange(range);
                } else if (range.startContainer.children && range.startContainer.children[range.startOffset - 1] && range.startContainer.children[range.startOffset - 1].classList.contains('object')) {
                        event.preventDefault();
                        selection.removeAllRanges();
                        range.selectNode(range.startContainer.children[range.startOffset - 1]);
                        selection.addRange(range);
                } else if (range.startContainer.children && range.startContainer.children[range.startOffset - 2] && range.startContainer.children[range.startOffset - 2].classList.contains('object')) {
                    event.preventDefault();
                    selection.removeAllRanges();
                    range.selectNode(range.startContainer.children[range.startOffset - 2]);
                    selection.addRange(range);
                } else if (range.startOffset === 0 && range.startContainer.parentElement.previousSibling && range.startContainer.parentElement.previousSibling.classList.contains('page-break')) {
                    event.preventDefault();
                    let newRange = iframe.createRange();
                    newRange.setStartAfter(range.startContainer.parentElement.previousSibling.previousSibling.firstChild);
                    newRange.setEndBefore(range.startContainer.parentElement);
                    selection.removeAllRanges();
                    selection.addRange(newRange);
                    iframe.execCommand('delete');
                    let newerRange = iframe.createRange();
                    newerRange.setStart(newRange.startContainer.nextSibling, 0);
                    newerRange.setEnd(newRange.startContainer.nextSibling, 0);
                    selection.removeAllRanges();
                    selection.addRange(newerRange);
                } else if (range.commonAncestorContainer.previousSibling && range.commonAncestorContainer.previousSibling.classList.contains('page-break')) {
                    event.preventDefault();
                    let newRange = iframe.createRange();
                    newRange.setStartAfter(range.startContainer.previousSibling.previousSibling.firstChild);
                    newRange.setEndBefore(range.startContainer);
                    selection.removeAllRanges();
                    selection.addRange(newRange);
                    iframe.execCommand('delete');
                    let newerRange = iframe.createRange();
                    newerRange.setStart(newRange.startContainer.nextSibling, 0);
                    newerRange.setEnd(newRange.startContainer.nextSibling, 0);
                    selection.removeAllRanges();
                    selection.addRange(newerRange);
                }
            }
        }
        
        this.handleKeyUpIframe(event);
    }

    handleKeyUpIframe(event) {
        let selection = document.getElementById('iframe').contentWindow.getSelection();
        let range = selection.getRangeAt(0);
        if (range.startContainer.parentElement && range.startContainer.parentElement.closest('.object')) {
            range.setStartBefore(range.startContainer.parentElement.closest('.object'));
            selection.removeAllRanges();
            selection.addRange(range);
        }

        if (range.endContainer.parentElement && range.endContainer.parentElement.closest('.object')) {
            range.setEndAfter(range.endContainer.parentElement.closest('.object'));
            selection.removeAllRanges();
            selection.addRange(range);
        }
    }

    handleClickIframe(event) {
        let iframe = document.getElementById('iframe').contentWindow.document;
        
        let object = event.target.closest('.object');

        if (iframe.querySelector('.selection')) {
            iframe.querySelector('.selection').classList.remove('selection');
        }

        if (object) {
            let selection = document.getElementById('iframe').contentWindow.getSelection();
            selection.removeAllRanges();
            object.classList.add('selection');
            document.getElementById('first').innerText = object.dataset.first;
            document.getElementById('second').innerText = object.dataset.second;
            document.getElementById('options').classList.add('focus');
            document.getElementById('options').style.top = object.getBoundingClientRect().top + iframe.body.scrollTop + 'px';
            document.getElementById('options').style.transform = 'translateY(-' + iframe.body.scrollTop + 'px)';
            this.object = object.dataset.object
            if (object.classList.contains('display')) {
                this.display = true;
                document.getElementById('display').classList.add('display');
            } else {
                this.display = false;
                document.getElementById('display').classList.remove('display');
            }
            document.getElementById('first').dataset.placeholder = this.objects[this.object][0];
            document.getElementById('second').dataset.placeholder = this.objects[this.object][1];
            document.getElementById('first').focus();
            selection = window.getSelection();
            let range = document.createRange();
            range.setStartAfter(document.getElementById('first').firstChild);
            selection.removeAllRanges();
            selection.addRange(range);
        } else {
            document.getElementById('options').classList.remove('focus');
        }
    }

    handleInputIframe(event) {
        let iframe = document.getElementById('iframe').contentWindow.document;
        let selection = document.getElementById('iframe').contentWindow.getSelection();
        let range = selection.getRangeAt(0);

        document.querySelector('title').innerText = this.props.file.replace(/.*\//, '');

        Array.from(iframe.getElementsByTagName('h1')).forEach((element, index) => {
            element.id = element.innerText.replace(/\s/g, '');
            if (index === 0) {
                document.querySelector('title').innerText = element.innerText;
            }
        });
        
        Array.from(iframe.querySelectorAll('h2, h3')).forEach((element) => {
            element.id = element.innerText.replace(/\s/g, '');
        });

        if (range.startContainer.id !== 'editor' && range.endContainer.id === 'editor') {
            let newRange = iframe.createRange();
            newRange.setStart(range.startContainer.nextSibling.nextSibling, 0);
            selection.removeAllRanges();
            selection.addRange(newRange);
        }

        document.getElementById('iframe').style.height = iframe.getElementById('editor').clientHeight + 'px';
    }
    
    handleInput(event) {
        let iframe = document.getElementById('iframe').contentWindow.document;
        if (iframe.querySelector('.selection')) {
            let range = iframe.createRange();
            let selection = document.getElementById('iframe').contentWindow.getSelection();
            range.selectNode(iframe.querySelector('.selection'));
            selection.removeAllRanges();
            selection.addRange(range);
        }

        if (event) {
            if (iframe.querySelector('.selection').classList.contains('display')) {
                this.display = true;
                document.getElementById('display').classList.add('display');
            } else {
                this.display = false;
                document.getElementById('display').classList.remove('display');
            }
        }

        iframe.getElementById('editor').focus();

        if (this.object === 'image') {
            iframe.execCommand('insertHTML', false, '<div class="selection object" data-object="image" data-first="' + document.getElementById('first').innerText.replace(/"/g, '&quot;') + '" data-second="' + document.getElementById('second').innerText.replace(/"/g, '&quot;') + '"><div class="image"><img src="' + (this.images[document.getElementById('first').innerText] || document.getElementById('first').innerText.replace(/"/g, '&quot;')) + '"/></div><div class="caption">' + document.getElementById('second').innerText.replace(/</g, '&lt;').replace(/>/g, '&gt;') + '</div></div>');
        } else if (this.object === 'link') {
            iframe.execCommand('insertHTML', false, '<span class="selection object" data-object="link" data-first="' + document.getElementById('first').innerText.replace(/"/g, '&quot;') + '" data-second="' + document.getElementById('second').innerText.replace(/"/g, '&quot;') + '"><a href="' + document.getElementById('first').innerText.replace(/"/g, '&quot;').replace(/\s/, '') + '">' + document.getElementById('second').innerText.replace(/</g, '&lt;').replace(/>/g, '&gt;') + '</a></span>');
        } else if (this.object === 'math') {
            iframe.execCommand('insertHTML', false, '<span class="selection object ' + (this.display ? 'display' : '') + '" data-object="math" data-first="' + document.getElementById('first').innerText.replace(/"/g, '&quot;') + '">' + window.katex.renderToString(document.getElementById('first').innerText, {displayMode: this.display, throwOnError: false, errorColor: '#DD4433'}).replace(/<span class="katex-mathml">.*<\/span><span class="katex-html"/s, '<span class="katex-html"') + '</span>');
        } else if (this.object === 'code') {
            iframe.execCommand('insertHTML', false, '<span class="selection object ' + (this.display ? 'display' : '') + '" data-object="code" data-first="' + document.getElementById('first').innerText.replace(/"/g, '&quot;') + '" data-second="' + document.getElementById('second').innerText + '">' + Prism.highlight(document.getElementById('first').innerText, Prism.languages.javascript, 'javascript') + '</span>');
        }

        document.getElementById('options').style.top = iframe.querySelector('.selection').getBoundingClientRect().top + iframe.body.scrollTop + 'px';
        document.getElementById('options').style.transform = 'translateY(-' + iframe.body.scrollTop + 'px)';
        if (event) {
            event.target.focus();
        } else {
            iframe.getElementById('editor').blur();
        }
    }

    handleKeyDown(event) {
        if (event.key === 'Escape') {
            if (!document.getElementById('first').innerText.match(/\S+/g)) {
                let iframe = document.getElementById('iframe').contentWindow.document;
                this.delete();
                document.getElementById('options').classList.remove('focus');
                iframe.getElementById('editor').focus();
            } else {
                let iframe = document.getElementById('iframe').contentWindow.document;
                let range = iframe.createRange();
                let selection = document.getElementById('iframe').contentWindow.getSelection();
                range.setStartAfter(iframe.querySelector('.selection'));
                range.setEndAfter(iframe.querySelector('.selection'));
                selection.removeAllRanges();
                selection.addRange(range);
                iframe.getElementById('editor').focus();
                document.getElementById('options').classList.remove('focus');
                iframe.querySelector('.selection').classList.remove('selection');
            }
        } else if (((navigator.platform === 'MacIntel' && event.metaKey) || (navigator.platform !== 'MacIntel' && event.ctrlKey)) && 'jghm'.indexOf(event.key.toLowerCase()) !== -1) {
            event.preventDefault();
        }
    }

    delete(event) {
        let iframe = document.getElementById('iframe').contentWindow.document;
        let range = iframe.createRange();
        let selection = document.getElementById('iframe').contentWindow.getSelection();
        let newRange = iframe.createRange();
        newRange.selectNode(iframe.querySelector('.selection'));
        selection.removeAllRanges();
        selection.addRange(newRange);
        iframe.execCommand('delete');
    }

    handleMouseDown(event) {
        if (document.getElementById('options').classList.contains('focus') && !document.getElementById('first').innerText.length) {
            this.delete();
        } else {
            let iframe = document.getElementById('iframe').contentWindow.document;
            document.getElementById('options').classList.remove('focus');
            if (iframe.querySelector('.selection')) {
                iframe.querySelector('.selection').classList.remove('selection');
            }
        }
    }

    handleClick(event) {
        let selection = window.getSelection();
        let range = selection.getRangeAt(0);
        event.target.classList.toggle('display');
        this.display = !this.display;
        this.handleInput();
        selection.removeAllRanges();
        selection.addRange(range);
    }

    handleChange(event) {
        let iframe = document.getElementById('iframe').contentWindow.document;
        let file = event.target.files[0];
        var reader = new FileReader();
        reader.onload = (event) => {
            iframe.getElementById('editor').focus();
            iframe.execCommand('selectAll');
            iframe.execCommand('insertHTML', false, event.target.result.match(/<div id="editor">(.*)<\/div>/s)[1]);
        }
        reader.readAsText(file);
    }

    render() {
        return (
            <div id="editor">
                <div id="options">
                    <pre id="first" spellCheck="false" contentEditable="true" onInput={(event) => this.handleInput(event)} onKeyDown={(event) => this.handleKeyDown(event)} onDrop={(event) => this.handleDrop(event)}></pre>
                    <pre id="second" spellCheck="false" contentEditable="true" onInput={(event) => this.handleInput(event)} onKeyDown={(event) => this.handleKeyDown(event)}></pre>
                    <input type="checkbox" id="display" onChange={(event) => this.handleClick(event)}/>
                    <label for="display"></label>
                </div>
                <input id="open" type="file" onChange={(event) => this.handleChange(event)}/>
                <iframe id="iframe" title="iframe"></iframe>
            </div>
        )
    }
}

export default App;